@font-face {
    /* 사용할 폰트의 이름 */
    font-family: "Do Hyeon";
    font-display: fallback;
  
    /* 
    src: 사용할 폰트의 위치 및 형식 
       Safari, Android, iOS => ttf
       Modern Browsers => woff
       IE6-IE8 => eot
    */
    src: url("./DoHyeon-Regular.ttf") format("truetype");
  
    /* 사용할 폰트의 기본 스타일 */
    font-weight: 400;
    font-style: normal;
  }

  @font-face {
   /* 사용할 폰트의 이름 */
   font-family: "NotoSansKR";
   font-display: fallback;
 
   /* 
   src: 사용할 폰트의 위치 및 형식 
      Safari, Android, iOS => ttf
      Modern Browsers => woff
      IE6-IE8 => eot
   */
   src: url("./NotoSansKR-Light.otf") format("truetype");
 
   /* 사용할 폰트의 기본 스타일 */
   font-weight: 400;
   font-style: normal;
 }