body{
    margin: 0;
  }
  
  div{
    font-family: "NotoSansKR", sans-serif; /* NotoSansKR 폰트를 적용 */
  }
  
  
  .content-header {
    height: 200px;
    width: 100vw;
    position: relative;
    background-position: top center;
    background-color: #a7ffeb;
    opacity: 0.88;
    text-align: center;
  }
  .content-header::before{
    content:"";
    background-image: url(../public/img/esg.png);
    background-size: cover;
    min-width: 100%;
    background-repeat: no-repeat;
    opacity: 1;   
    position:absolute; 
    top:0px;
    left:0px;
    right:0px;
    bottom:0px;
    z-index: -1;
  }

  .content-header strong{
    line-height: 200px; /*div height와 같게 해야함*/
    text-align: center;
    display: block;
    font-size: 50px;
    color: white;
  }
  
  .content-body{
    position: relative;
    float: left;
    display: table;
    width: 80vw;
    height: 100vh;
    margin-left: 10vw;
    text-align: center;
  
  }
  
  .content-body-1{
    margin-top: 50px;
    font-size: 35px;
  }
  
